import { Component, OnInit, Input, HostBinding, Output,  EventEmitter, ElementRef, ViewChild, ChangeDetectorRef  } from '@angular/core';
import { AbstractControl, FormBuilder } from '@angular/forms';
import { ControlContainer, FormGroupDirective } from '@angular/forms';
import { DaoBaseService } from 'src/app/dao/dao-base.service';
import { Base, IIndexable } from 'src/app/models/base.model';
import * as moment from 'moment';
import { LookupItem } from 'src/app/services/lookup.service';

export type SelectItem = {
  value: any,
  text: string,
  entity: any
} | null;

@Component({
  selector: 'input-label',
  templateUrl: './input-label.component.html',
  styleUrls: ['./input-label.component.scss'],
  viewProviders: [
    {
      provide: ControlContainer,
      useExisting: FormGroupDirective
    }
  ]
})
export class InputLabelComponent implements OnInit {
  @ViewChild('dateInput') dateInput?: ElementRef;
  @ViewChild('timeInput') timeInput?: ElementRef;
  @HostBinding('class') class = 'form-group';
  @Output() change = new EventEmitter<Event>();
  @Output() buttonClick = new EventEmitter<Event>();
  @Output() select = new EventEmitter<SelectItem>();
  @Output() onDetails = new EventEmitter<SelectItem>();
  @Input() value: any = "";
  @Input() textCase: string = "";
  @Input() type: string = "text";
  @Input() label: string = "";
  @Input() bold: boolean = false;
  @Input() details: boolean = false;
  @Input() itemTodos: string = "";
  @Input() controlName: string | null = null;
  @Input() control?: AbstractControl = undefined; 
  @Input() size: number = 0;
  @Input() rows: number = 0;
  @Input() disabled?: boolean = undefined;
  @Input() readonly?: boolean = undefined;
  @Input() items: LookupItem[] = [];
  @Input() iconButton: string = "bi-search";
  @Input() placeholder: string = "";
  @Input() fields: string[] = [];
  @Input() dao?: DaoBaseService<Base> = undefined;
  @Input() loading: boolean = false;

  private DEBOUNCE_TIMER = 1000;
  private queryText: string = "";
  private noCatchTyped: boolean = false;
  private timer: any = undefined;
  private selectedItem?: { value: any, text: string } = undefined;  
  private selectedValue?: string = undefined;
  public researching: boolean = false;
  public searchObj: any = undefined;
  public isFirefox = navigator.userAgent.toLowerCase().indexOf('firefox') > -1;

  constructor(
    private elem: ElementRef, 
    private cdRef: ChangeDetectorRef,
    private fb: FormBuilder
  ) {
    elem.nativeElement.component = this;
  }

  ngOnInit(): void {
    const self = this;
    if(this.size > 0) this.class += " col-md-"+this.size;
    if(this.inTypes(["date", "datetime"])) {
      if(this.control) {
        this.value = this.control.value;
        this.control.valueChanges.subscribe(newValue => {
          this.value = newValue;
        });
      }
    } else if(this.isType("search") && !this.readonly){
      $(() => {
        //@ts-ignore
        $('#' + this.controlName).autoComplete({
          resolver: 'custom',
          events: {
            search: (qry: string, callback: any) => {
              this.researching = true;
              this.dao?.searchText(qry, this.fields.length ? this.fields : undefined).then(result => {
                this.researching = false;
                if(this.queryText == qry){
                  callback(result);
                } else {
                  $('#' + this.controlName).trigger("keyup");
                }
              }).catch(error => {
                this.researching = false;
              });
            },
            typed: (newValue: string, origJQElement: any) => {
              this.queryText = newValue;
              if(this.noCatchTyped && !this.loading){
                this.noCatchTyped = false;
                return newValue;
              } else {
                if(this.timer) clearTimeout(this.timer);
                this.timer = setTimeout(() => {
                  this.noCatchTyped = !this.researching;
                  $('#' + this.controlName).trigger("keyup");
                }, this.DEBOUNCE_TIMER);
                return false;
              }
            }
          }
        });
        $('#' + this.controlName).on('autocomplete.select', function (evt, item) {
          if(self.control) {
            const selectedValue = item ? item.value : "";
            self.selectedValue = selectedValue;
            self.control.setValue(selectedValue);
            self.searchObj = undefined;
            if(self.control.value.length){
              self.dao?.getById(self.control.value).then(result => {
                self.searchObj = result;
                self.selectedItem = item;
                if(self.select) self.select.emit({ ...item, entity: result });
              });
            }
          } else if(self.select) {
            self.select.emit(item);
          } 
        });
      });
      if(this.control) {
        this.control.valueChanges.subscribe(newValue => {
          if(this.selectedValue != newValue) {
            this.selectedValue = newValue;
            this.loadSearchText();
          }
        });
      }
    } else if(this.isType("select") && this.dao) {
      this.dao.searchText("", this.fields.length ? this.fields : undefined).then(result => {
        //{value: string, text: string}
        this.items = result.map(x => {
          return {
            key: x.value,
            value: x.text
          };
        }) || [];
      });
    }
  }

  public onChangeDateTime(event: any) {
    const strDate = this.dateInput?.nativeElement.value || "";
    const strTime = this.timeInput?.nativeElement.value || "";
    const oldValue = this.value;
    try {
      this.value = new Date(this.isFirefox ? strDate + "T" + strTime : strDate);
      if(oldValue && this.value && oldValue.toString() != this.value.toString()) {
        this.cdRef.markForCheck();
      }      
    } catch (e: any) {
      this.value = null;
    }
    this.control?.setValue(this.value);
  }

  public getDateValue() {
    return !this.value || !(this.value instanceof Date) ? null : this.isFirefox ? moment(this.value).format("YYYY-MM-DD") : moment(this.value).format("YYYY-MM-DDTHH:mm:ss.SSS");
  }

  public getTimeValue() {
    return !this.value || !(this.value instanceof Date) ? null : moment(this.value).format("HH:mm");
  }

  public loadSearchText(){
    if(this.control?.value?.length){
      this.researching = true;
      this.dao?.getById(this.control.value).then(result => {
        const entity = result as IIndexable;
        const text = (this.fields.length ? this.fields : this.dao!.searchFields).map((item: string) => entity[item]).join(" - ");
        const item = { value: entity["id"], text: text };
        this.searchObj = result;
        this.researching = false;
        this.selectedItem = item;
        if(this.readonly) {
          this.value = text;
        } else {
          //@ts-ignore
          $('#' + this.controlName).autoComplete('set', this.selectedItem);
        }
        if(this.select) this.select.emit({ ...item, entity: result });
      }).catch(() => {
        this.researching = false;
      });
    } else {
      $('#' + this.controlName).trigger("keyup");
    }
  }

  public clear() {
    if(this.isType("search") && !this.readonly){
      //@ts-ignore
      $('#' + this.controlName).autoComplete('set', null);
      this.searchObj = undefined;
      if(this.control) {
        this.control.setValue("");
      }
    }
  }
    
  public formattedDateTime(dataHora: any, apenasData: boolean = false): string {
    apenasData = apenasData ? true : this.type.toLowerCase() == "date";
    return dataHora ? moment(dataHora instanceof Date ? dataHora : dataHora.toDate()).format(apenasData ? "DD/MM/YYYY" : "DD/MM/YYYY HH:mm") : "";
  }

  public getSelectDisabledText(): string {
    let key = this.control ? this.control.value : this.value;
    return this.items.find(item => item.key == key)?.value || "";
  }

  public inTypes(types: string[]): boolean {
    return types.indexOf(this.type.toLowerCase()) >= 0;
  }

  public isType(type: string): boolean{
    return this.type.toLowerCase() == type;
  }

  public isInvalid(): boolean {
    return this.readonly || this.disabled ? false : !this.control! ? true : this.control!.invalid && (this.control!.dirty || this.control!.touched);
  }

  public hasError(): boolean {
    return !this.control! || this.readonly ? false : !!this.control!.errors;
  }

  public errorMessage() {
    return this.control!.errors?.errorMessage;
  }

  public onChange(event: Event){
    if(this.change) this.change.emit(event);    
  }

  public onButtonClick(event: Event){
    if(this.buttonClick) this.buttonClick.emit(event);    
  }

  public onDetailsClick(event: Event){
    if(this.onDetails && this.selectedItem && this.searchObj) this.onDetails.emit({...this.selectedItem, entity: this.searchObj});
  }
}