import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.component.scss']
})
export class FilterComponent implements OnInit {
  @Output() onFilterSubmit = new EventEmitter<void>();
  @Output() onFilterClear = new EventEmitter<void>();
  @Input() form?: FormGroup;
  @Input() clear?: (filter: FormGroup) => void;
  @Input() where?: (filter: FormGroup) => any[];
  @Input() collapsed: boolean = true;

  constructor() { }

  ngOnInit(): void {
  }

  public toggle() {
    this.collapsed = !this.collapsed;
  }

  public onButtonClearClick() {
    this.onFilterClear.emit();
  }

  public onButtonFilterClick() {
    this.onFilterSubmit.emit();
  }
}
