import { ChangeDetectorRef, Component, ContentChild, ContentChildren, EventEmitter, Injector, Input, OnInit, Output, QueryList, ViewChildren } from '@angular/core';
import { Observable, of } from 'rxjs';
import { DaoBaseService, queryOrderBy } from 'src/app/dao/dao-base.service';
import { QueryContext } from 'src/app/dao/query-context';
import { QueryOptions } from 'src/app/dao/query-options';
import { Base } from 'src/app/models/base.model';
import { FullRoute, NavigateService, RouteMetadata } from 'src/app/services/navigate.service';
import { ToolbarButton, ToolbarComponent } from '../toolbar/toolbar.component';
import { ColumnComponent } from './column/column.component';
import { FilterComponent } from './filter/filter.component';
import { GridColumn } from './grid-column';
import { PaginationComponent } from './pagination/pagination.component';

@Component({
  selector: 'grid',
  templateUrl: './grid.component.html',
  styleUrls: ['./grid.component.scss']
})
export class GridComponent implements OnInit {
  @ContentChildren(ColumnComponent, { descendants: true }) columnsRef?: QueryList<ColumnComponent>;
  @ContentChild(FilterComponent) filterRef?: FilterComponent;
  @ContentChild(ToolbarComponent) toolbarRef?: ToolbarComponent;
  @ContentChild(PaginationComponent) paginationRef?: PaginationComponent;
  @Output() onLoadList = new EventEmitter<Base[]>();
  @Input() dao?: DaoBaseService<Base>;
  @Input() list?: Observable<any[]>;
  @Input() addRoute?: FullRoute;
  @Input() addMetadata?: RouteMetadata;
  @Input() orderBy?: queryOrderBy[];
  @Input() join: string[] = [];
  @Input() rowsLimit: number = 10;
  @Input() set query(value: QueryContext<Base> | undefined) {
    this._query = value;
    if(value) {
      this.list = value.subject.asObservable();
    }
  }
  get query(): QueryContext<Base> | undefined {
    return this._query;
  }
  @Input() set values(value: any[] | undefined) {
    this._values = value;
    if(value) {
      this.list = of(value);
    }
  }
  get values(): any[] | undefined {
    return this._values;
  }

  /* Propriedades private e mtodos get e set */
  private _query?: QueryContext<Base>;
  private _values?: any[];
  private _error?: string;

  /* Propriedades publicas */
  public self: GridComponent = this;
  public columns: GridColumn[] = [];
  public toolbarButtons: ToolbarButton[] = [];
  public cdRef: ChangeDetectorRef;
  public go: NavigateService;
  public set error(error: string | undefined) {
    this._error = error;
  }
  public get error(): string | undefined {
    return this._error;
  }

  constructor(public injector: Injector) { 
    this.cdRef = this.injector.get<ChangeDetectorRef>(ChangeDetectorRef);
    this.go = this.injector.get<NavigateService>(NavigateService);
    this.dao = new DaoBaseService<Base>("", injector);
    this.toolbarButtons = [
      {
        route: this.addRoute,
        icon: "bi bi-search",
        label: "Filtrar",
        onClick: () => this.filterRef?.toggle()
      },
      {
        icon: "bi bi-plus-circle",
        color: "btn-outline-success",
        label: "Incluir",
        onClick: () => this.go.navigate(this.addRoute!, this.addMetadata)
      }
    ];
  }

  ngOnInit(): void {
  }

  ngAfterContentInit(): void {
    /* Carrega as configuraes feitas via components (tags) */
    this.loadColumns();
    this.loadFilter();
    this.loadToolbar();
    this.loadPagination();
  }

  public queryInit() {
    this.query = this.dao?.query(this.queryOptions, { after: () => this.cdRef.detectChanges() });
    this.cdRef.detectChanges();
    this.list?.subscribe(rows => {
      if(this.onLoadList) this.onLoadList.emit(rows);
    });
  }

  private get queryOptions(): QueryOptions {
    return {
      where: this.filterRef?.where && this.filterRef?.form ? this.filterRef?.where(this.filterRef.form) : [],
      orderBy: this.orderBy || [],
      join: this.join || [],
      limit: this.rowsLimit
    };
  }

  public loadFilter() {
    if(this.filterRef) {
      this.filterRef.onFilterSubmit.subscribe(() => {
        this.query?.reload(this.queryOptions);
      });
      this.filterRef.onFilterClear.subscribe(() => {
        if(this.filterRef?.form) {
          this.filterRef.form.reset();
          if(this.filterRef.clear) this.filterRef.clear(this.filterRef.form);
          this.filterRef.onFilterSubmit.emit();
        }
      });
    }
  }

  public loadToolbar() {
    if(this.toolbarRef) {
      this.toolbarRef.buttons = this.toolbarButtons;
    }
  }

  public loadPagination() {
    if(this.paginationRef) {
      this.paginationRef.query = this.query;
    }
  }

  public loadColumns() {
    this.columns = [];
    this.columnsRef?.forEach(column => {
      this.columns.push(Object.assign(new GridColumn(), {
        title: column.title,
        type: column.type,
        field: column.field,
        orderBy: column.orderBy,
        editable: column.editable,
        template: column.template,
        itens: column.items,
        options: column.options,
        buttons: column.type != "options" ? undefined : column.buttons || [
          { icon: "bi bi-pencil-square", color: "btn-outline-info", onClick: column.onEdit },
          { icon: "bi bi-trash", color: "btn-outline-danger", onClick: column.onDelete }
        ]
      }));
    });
  }
}
